.header {
  width: 100%;
  z-index: 50;
  border-bottom: 1px solid #fff;
  flex-wrap: wrap;
  position: fixed;
  top: 0;
  left: 0;
}

.header-menu {
  transition: all .3s;
}

.container {
  max-width: 1920px;
