  font-style: normal;
  font-weight: 500;
  line-height: normal;
  left: 0;
}

.nav-ref {
  color: #fffcf6;
  text-transform: uppercase;
  font-family: Helvetica;
  font-size: .875em;
  font-style: normal;
  font-weight: 400;
  line-height: 142.857%;
}

.nav-list {
  z-index: 2;
  flex-wrap: wrap;
  gap: 70px;
  display: flex;
  position: relative;
}

.header-num {
  z-index: 2;
  margin-left: 10vw;
}

.header-burger {
  display: none;
}

@media (max-width: 1024px) {
  body.lock {
    overflow: hidden;
  }

  .header-body {
    height: 50px;
  }

  .nav-list {
    display: block;
  }

  .header-burger {
    z-index: 3;
    width: 30px;
    height: 20px;
    margin: 0 0 0 20px;
    display: block;
    position: relative;
  }

  .header-burger span {
    width: 100%;
    height: 2px;
    background-color: #fffcf6;
    position: absolute;
    top: 9px;
    left: 0;
  }

  .header-burger:before, .header-burger:after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #fffcf6;
    list-style: none;
    display: block;
    position: absolute;
    left: 0;
  }

  .header-burger:before {
    top: 0;
  }

  .header-burger:after {
    bottom: 0;
  }

  .header-menu {
    width: 100%;
    height: 100%;
    background-color: #313f38;
    gap: 40px;
    padding-top: 80px;
    position: fixed;
    top: -100%;
    left: 0;
    overflow: auto;
  }

  .header-menu.active {
    top: 0;
  }

  .header-burger.active:before {
    top: 9px;
    transform: rotate(45deg);
  }

  .header-burger.active:after {
    top: 9px;
    transform: rotate(-45deg);
  }

  .header-burger.active span {
    transform: scale(0);
  }
}

